import React from "react";
import data from "../../info.json";
import { useNavigate } from "react-router-dom";

export default function Perfil() {
  function Horario() {
    const fecha = new Date();
    var semana = fecha.getDay();
    return (
      <>
        {data.horarios.map((objeto, index) => (
          <div className="horario">
            <div className={index == semana ? "dia dia-actual" : "dia"}>
              {objeto.dia}
            </div>
            {index == semana ? <hr className="linea" /> : <h1></h1>}
            <div className={index == semana ? "horas dia-actual" : "horas"}>
              {objeto.apertura[0] >= 12
                ? (objeto.apertura[0] % 12 == 0
                    ? 12
                    : objeto.apertura[0] % 12) +
                  ":" +
                  objeto.apertura[1] +
                  "0 PM"
                : (objeto.apertura[0] % 12 == 0
                    ? 12
                    : objeto.apertura[0] % 12) +
                  ":" +
                  objeto.apertura[1] +
                  "0 AM"}
              -
              {objeto.cierre[0] >= 12
                ? (objeto.cierre[0] % 12 == 0 ? 12 : objeto.cierre[0] % 12) +
                  ":" +
                  objeto.cierre[1] +
                  "0 PM"
                : (objeto.cierre[0] % 12 == 0 ? 12 : objeto.cierre[0] % 12) +
                  ":" +
                  objeto.cierre[1] +
                  "0 AM"}
            </div>
          </div>
        ))}
      </>
    );
  }
  let navigate = useNavigate();
  const ToHome = () => {
    navigate("../Cafe");
  };
  return (
    <div>
      <nav className="navegacionBarra">
        <button onClick={ToHome}>
          <ion-icon name="arrow-back"></ion-icon>
        </button>
      </nav>
      <div style={{ marginBottom: "10px" }}>
        <div className="head">
          <img src={data.url} />
          <h2>{data.nombreCafeteria}</h2>
          <p>{data.parrafo}</p>
        </div>
        <div className="location">
          <ion-icon className="ubi" name="pin"></ion-icon>
          <a href={data.ubicacion}>Como llegar</a>
        </div>
      </div>
      <section className="container container-features">
        <div className="card-feature">
          <ion-icon name="airplane"></ion-icon>
          <div className="feature-content">
            <span>Envío gratuito a nivel mundial</span>
            <p>En pedido superior a $150</p>
          </div>
        </div>
        <div className="card-feature">
          <ion-icon name="card"></ion-icon>
          <div className="feature-content">
            <span>Contrareembolso</span>
            <p>100% garantía de devolución de dinero</p>
          </div>
        </div>
        <div className="card-feature">
          <ion-icon name="headset"></ion-icon>
          <div className="feature-content">
            <span>Tarjeta regalo especial</span>
            <p>Ofrece bonos especiales con regalo</p>
          </div>
        </div>
        <div className="card-feature">
          <ion-icon name="cart"></ion-icon>
          <div className="feature-content">
            <span>Servicio al cliente 24/7</span>
            <p>LLámenos 24/7 al 123-456-7890</p>
          </div>
        </div>
      </section>
      <section className="Semana">
        <h1>Horario semanal</h1>
        <Horario />
      </section>
    </div>
  );
}
