import { supabase } from "../../supabase/supabase";
import "./body.css";
import { useEffect, useState } from "react";
import SlideFav from "./SlideFav/SlideFav";
import Menu from "../Barnav-sup/MenuBar/MenuBar";
import Cuerpo from "./Cuerpo/cuerpo";
import SearchBar from "../Barnav-sup/searchBar";
import Modal from "./modal";

export default function Body() {
  const [product_cafe, setCafe] = useState([]);
  const [Prov, setProv] = useState([]);
  const [product_Ff, setFf] = useState([]);
  const [favGlobal, setFav] = useState([]);
  const [hov, setHov] = useState("hov");
  const [hove, setHove] = useState(null);
  const [Out, setOut] = useState(true);
  const [items, setItems] = useState([]);
  const [pago, setpago] = useState(0);
  const [count, setcount] = useState(0);
  const [viewModal, setviewModal] = useState(false);
  const [PassModal, setPassModal] = useState([]);
  const [loading, setLoading] = useState(true);

  //Datos de Supabase
  useEffect(() => {
    const fetchBebidas = async () => {
      const { data: Coffee, error1 } = await supabase.from("Coffee").select();
      if (error1) {
        alert(error1);
      }
      Separar(Coffee);
      setProv(Coffee);
      setLoading(false);
    };
    fetchBebidas();
  }, []);
  function Separar(Coffee) {
    setPassModal(Prov[0]);
    const cajas = [];
    Coffee.map((element) => {
      cajas.push(element.caja);
    });
    //Eliminar los repetido
    const objetosDif = [...new Set(cajas)];
    const objetosCafe = [];
    objetosDif.map((element) => {
      const aux = Coffee.filter((object) => object.caja === element);
      objetosCafe.push(aux);
    });
    setCafe(objetosCafe);
    setFf(Coffee);
  }
  //Evitar Scroll cuando Modal is Open
  useEffect(() => {
    if (viewModal) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }
  }, [viewModal]);
  useEffect(() => {
    if (loading) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }
  }, [loading]);

  useEffect(() => {
    const favoritos = Prov.filter(
      (objeto) => objeto.favorito == true && objeto.agotado == false
    );
    setFav(favoritos);
  }, [product_cafe]);

  function handleClick1() {
    window.scroll({ top: 0, behavior: "smooth" });
    setHov("hov");
    setHove(null);
    setOut(true);
  }
  function handleClick2() {
    window.scroll({ top: 0, behavior: "smooth" });
    setHov(null);
    setHove("hov");
    setOut(false);
  }
  //Aumentar en 1 la cantidad de pedidos en el carrito
  let pagar = 0;
  const array = [];
  array.push(...items);
  const enviarCart = (newItem) => {
    const nuevoArreglo = Prov.filter((objeto) =>
      objeto.title === newItem.title
        ? { ...objeto, disable: (objeto.disable = false), Cant: objeto.Cant++ }
        : objeto
    );
    Refrescar(nuevoArreglo);
  };
  //Decrementar la cantidad de pedidos en el carrito
  const restarCart = (newItem) => {
    const nuevoArreglo = Prov.filter((objeto) =>
      objeto.title == newItem.title
        ? {
            ...objeto,
            disable:
              objeto.Cant > 1
                ? (objeto.disable = false)
                : (objeto.disable = true),
            Cant: objeto.Cant > 1 ? objeto.Cant-- : (objeto.Cant = 0),
          }
        : objeto
    );

    Refrescar(nuevoArreglo);
  };

  //Incremento y Decremento
  function clickMinus(e) {
    const id = e.target.parentNode.id;
    const nArray = items.filter((objeto) => objeto.title == id);
    restarCart(nArray[0]);
  }
  function clickPlus(e) {
    const id = e.target.parentNode.id;
    const nArray = items.filter((objeto) => objeto.title == id);
    enviarCart(nArray[0]);
  }
  //Refrescar los valores q se muestran en pantalla
  function Refrescar(arr) {
    const ArrayView = arr.filter((objeto) => objeto.Cant >= 1);
    if (!ArrayView[0]) {
      setOut(true);
    }
    setItems(ArrayView);
    pagar = 0;
    let cc = 0;
    ArrayView.map((objeto) => (pagar = pagar + objeto.price * objeto.Cant));
    ArrayView.map((objeto) => (cc = cc + objeto.Cant));
    setpago(pagar);
    setcount(cc);
    Separar(Prov);
  }
  function filtredObject(obj) {
    if (obj) {
      const filtred = Prov.filter((object) =>
        object.title.toLowerCase().includes(obj.toLowerCase())
      );
      Separar(filtred);
    } else {
      Separar(Prov);
    }
  }
  function windowModal(array) {
    setPassModal(array);
    setviewModal(true);
    console.log(viewModal);
    console.log(array);
  }
  function addMensaje(aa) {
    setviewModal(aa);
  }
  return (
    <div>
      {loading && <LoadingMask />}

      {Out ? (
        <>
          <SearchBar filtredObject={filtredObject} />
          <Menu />
          <main id="show">
            <SlideFav favGlobal={favGlobal} />

            <div class="cajon" id="ofertas">
              {product_cafe.map((objeto) => (
                <Cuerpo
                  objeto={objeto}
                  enviarCart={enviarCart}
                  restarCart={restarCart}
                  windowModal={windowModal}
                />
              ))}
            </div>
          </main>
        </>
      ) : (
        <main id="show1">
          <div class="container-pago">
            <div id="root"></div>
            <div class="sidebar">
              <div class="head">
                <p>Mis Pedidos</p>
              </div>
              <div className="carrito">
                {items.map((objeto) => (
                  <div class="cart-item">
                    <div class="row-img">
                      <img class="rowimg" src={objeto.image} />
                    </div>
                    <div className="flex">
                      <p style={{ fontSize: "12px" }}>{objeto.title}</p>
                      <h2 style={{ fontSize: "15px" }}>$ {objeto.price}.00</h2>
                    </div>
                    <div className="contCarrito" id={objeto.title}>
                      <button className="MinusPlus" onClick={clickMinus}>
                        -
                      </button>
                      <h4>{objeto.Cant}</h4>
                      <button className="MinusPlus" onClick={clickPlus}>
                        +
                      </button>
                    </div>
                  </div>
                ))}
              </div>

              <div class="foot">
                <h3>Total</h3>
                <h2 id="total">$ {pago}.00</h2>
              </div>
              <button>Pagar</button>
            </div>
          </div>
        </main>
      )}
      {items[0] && (
        <nav class="fav">
          {!Out ? (
            <a class={hov} onClick={handleClick1}>
              <h2>OFERTAS</h2>
              <ion-icon
                style={{ marginLeft: 5 + "px" }}
                name="clipboard"
              ></ion-icon>
            </a>
          ) : (
            <a class={hove} onClick={handleClick2}>
              <h2>CARRITO</h2>
              <ion-icon style={{ marginLeft: 5 + "px" }} name="cart"></ion-icon>
              <p id="count">{count}</p>
            </a>
          )}
        </nav>
      )}
      {viewModal && (
        <section class={viewModal ? "modal modal--show" : "modal"}>
          <Modal PassModal={PassModal} addMensaje={addMensaje} />
        </section>
      )}
    </div>
  );
}
const LoadingMask = () => {
  return (
    <div className="loading-Mask">
      <ion-icon name="cafe"></ion-icon>
      <p style={{ color: "white" }}>Cargando...</p>
    </div>
  );
};
