import { useState, useEffect } from "react";
import "./body.css";

export default function Modal({ PassModal, addMensaje }) {
  const [Array, setArray] = useState({
    image: "",
    title: "",
    descripcion: [],
    price: "",
  });
  useEffect(() => {
    setArray(PassModal);
  }, [PassModal]);

  const CloseModal = () => {
    addMensaje(false);
  };
  return (
    <div class="modal__container">
      <img src={Array.image} class="modal__img" />
      <h2 class="modal__title">{Array.title}</h2>
      <div class="sabores">
        <ul>
          {Array.descripcion.map((element) => (
            <li>{element}</li>
          ))}
        </ul>
      </div>
      <p class="price">${Array.price}.00</p>

      <a class="modal__close" onClick={CloseModal}>
        Cerrar
      </a>
    </div>
  );
}
