import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./barnav.css";
import data from "../../info.json";
import logo from "../../Logo.png";
import MenuDesplegable from "./MenuDesplegable";

const today = new Date();
const fechaApertura = new Date();
const fechaCierre = new Date();
var semana = today.getDay();
var OpenClose = null;
var colores;

fechaApertura.setHours(
  data.horarios[semana].apertura[0],
  data.horarios[semana].apertura[1],
  0,
  0
);
if (data.horarios[semana].apertura[0] >= data.horarios[semana].cierre[0]) {
  fechaCierre.setDate(today.getDate() + 1);
}
fechaCierre.setHours(
  data.horarios[semana].cierre[0],
  data.horarios[semana].cierre[1],
  0,
  0
);
if (fechaApertura <= today && fechaCierre >= today) {
  OpenClose = "Abierto";
} else {
  OpenClose = "Cerrado";
}
export function Profile() {
  if (OpenClose == "Abierto") {
    colores = " #994f09";
  } else if (OpenClose == "Cerrado") {
    colores = " red";
  }

  return <h4 style={{ color: colores, fontSize: "15px" }}>{OpenClose}</h4>;
}

export default function Barnav() {
  const [ActMenu, setActMenu] = useState(false);
  const result = data.nombreCafeteria.substring(
    data.nombreCafeteria.indexOf(" ") + 1
  );
  let navigate = useNavigate();
  const ToHome = () => {
    navigate("../Cafe/Perfil/");
  };
  useEffect(() => {
    if (ActMenu) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }
  }, [ActMenu]);
  const cambioMenu = () => setActMenu(true);
  const Cambio2 = () => setActMenu(false);
  return (
    <>
      <nav class="barnav">
        <button onClick={ToHome}>
          <img src={logo} alt={logo} />
          <div>
            <h3> {result}</h3>
            <Profile />
          </div>
        </button>

        <button onClick={cambioMenu} id="hora">
          <ion-icon name="menu"></ion-icon>
        </button>
      </nav>
      {ActMenu && <MenuDesplegable Cambio2={Cambio2} />}
    </>
  );
}
