import { useEffect, useState } from "react";
import React from "react";
import Barnav from "./Components/Barnav-sup/barnav";
import Body from "./Components/Body/body";
import "./style.css";
import Footer from "./Components/Footer/footer";
import Login from "./Components/Login/login";
import Autho from "./Components/Login/Auth";
import { Route, Routes, useNavigate } from "react-router-dom";
import data from "./info.json";
import Perfil from "./Components/Perfil/perfil";

function App() {
  useEffect(() => {
    document.title = data.sitioWeb; // Cambia aquí el nuevo título
  }, []);

  function nigth() {
    const root = document.documentElement;
    root.style.setProperty("--primary-color", "#d37014");
    root.style.setProperty("--second-color", "#302f2f");
    root.style.setProperty("--background-color", "#292827");
    root.style.setProperty("--font-color", "#ffffff");
    root.style.setProperty("--font2-color", "#cecece");
    root.style.setProperty("--body-color", "#000000");
  }
  const fecha = new Date();
  var hora = fecha.getHours();
  if (hora >= 19 || hora <= 6) nigth();

  return (
    <div>
      <div class="container" className="App" style={{ width: "100vw" }}>
        <Routes>
          <Route path="Cafe/login/" element={<Login />} />
          <Route path="Cafe/Auth/" element={<Autho />} />
          <Route path="Cafe/Perfil/" element={<Perfil />} />
          <Route
            path="Cafe/"
            element={
              <>
                <Barnav />
                <Body />
                <Footer />
              </>
            }
          />
        </Routes>
      </div>
    </div>
  );
}

export default App;
